    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center; */
}

.nav-wrap {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: calc(100% - 180px);
    margin: auto; 
}

.nav-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style: none;
    max-height: 50px;
}

.nav-item {
    display: block;
    margin-left: 20px;
    font-size: 18px;
    line-height: 23px;
    text-align: center;
    color: #040192;
    font-weight: bold;
    opacity: 0.9;
}

.nav-item:hover {
    cursor: pointer;
    color: #262627;
    opacity: 0.8;
}

.nav-contacts {
    color: #040192;
    font-weight: bold;
    opacity: 0.9;
}

.logo-nav {
    height: 40px;
    width: 180px;
}


@media (max-width: 530px) {
    .nav-item {
        font-size: 12px;
    }

    .nav-contacts {
        font-size: 12px;
    }
}